/**
 * Avatar
 */
.avatar {
  width: 96px;
  height: 96px;
  color: #ffa941;
  overflow: hidden; }
  .avatar svg {
    width: 100%;
    fill: currentColor; }

/** Sizes
--------------------------------------------------*/
.avatar--size-auto {
  width: 100%;
  height: 100%; }

.avatar--size-small {
  width: 48px;
  height: 48px; }

.avatar--size-large {
  width: 192px;
  height: 192px; }

/** Looks
--------------------------------------------------*/
.avatar--circle {
  border-radius: 100%;
  border: 1px solid #ffa941; }

/** Colors
--------------------------------------------------*/
.avatar--color-primary {
  color: #ffa941;
  border-color: #ffa941; }

.avatar--color-secondary {
  color: #4648d2;
  border-color: #4648d2; }

.avatar--color-red {
  color: #FA3D49;
  border-color: #FA3D49; }

.avatar--color-orange {
  color: #ffa941;
  border-color: #ffa941; }

.avatar--color-yellow {
  color: #ffe433;
  border-color: #ffe433; }

.avatar--color-green {
  color: #4BC45F;
  border-color: #4BC45F; }

.avatar--color-blue {
  color: #3333f3;
  border-color: #3333f3; }

.avatar--color-indigo {
  color: #178cd6;
  border-color: #178cd6; }

.avatar--color-violet {
  color: #8b26f1;
  border-color: #8b26f1; }

.avatar--color-black {
  color: black;
  border-color: black; }

.avatar--color-gray {
  color: #999999;
  border-color: #999999; }

.avatar--color-white {
  color: white;
  border-color: white; }

/**
 * Breadcrumbs
 */
.breadcrumbs {
  display: flex;
  /** Menu
  --------------------------------------------------*/ }
  .breadcrumbs > ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    user-select: none;
    /** Item
    --------------------------------------------------*/
    /** Link
    --------------------------------------------------*/
    /** Icon
    --------------------------------------------------*/ }
    .breadcrumbs > ul > li {
      font-size: 14px;
      margin-right: 3px; }
      .breadcrumbs > ul > li:first-child > a {
        margin-left: 0;
        padding-left: 12px; }
        .breadcrumbs > ul > li:first-child > a::before {
          display: none; }
      .breadcrumbs > ul > li:first-child [class^="icon-"], .breadcrumbs > ul > li:first-child [class*=" icon-"] {
        margin-left: -3px; }
      .breadcrumbs > ul > li:last-child {
        margin-right: 0; }
        .breadcrumbs > ul > li:last-child > a {
          padding-right: 18px; }
